import { Grid, makeStyles, Typography } from '@material-ui/core';
import React, { useState } from 'react'
import { useEffect } from 'react'
import api from '../../axios/API';
import { EmptyData } from '../../components/gen/EmptyData';
import { CardProject } from '../HomeView/components_socios/CardProject';
import { pathApi } from '../LoginView/app_const/AppConst';

const useStyle = makeStyles({
    gridContainerProject: {
        padding: '2rem 2rem 2rem 2rem',
        minHeight: "76vh"
    }
})

export const ProjectPendingView = () => {

    const classes = useStyle();

    const [ProjectPending, setProjectPending] = useState([]);

    const [filter, setFilter] = useState([]);

    useEffect(() => {
        let mounted = true;

        const getProjectPending = async () => {
            try {
                const resp = await api(pathApi.pendingProject);
                setProjectPending(resp.data.data);
            } catch (e) {
                console.log(e);
            }
        }

        if (mounted) {
            getProjectPending();
        }

        return () => {
            mounted = false;
        }
    }, [])

    if (ProjectPending.filter(p => !filter.includes(p.id)).length === 0) return <Grid container justify={"center"} alignContent={"center"}
                                                          direction={"column"}> <EmptyData not_data={"Sin proyectos pendientes"}/>
    </Grid>;

    return (
        <>
            <Grid container justify="center">
                <Typography variant="h2">Proyectos pendientes revision</Typography>
            </Grid>
            <Grid container justify={"center"} className={classes.gridContainerProject}>

                {
                    ProjectPending.filter(p => !filter.includes(p.id)).map(p => <CardProject key={p.id} {...p} buttonEnabled={true} setFilter={setFilter} filter={filter} />)
                } 
            </Grid>
        </>
    )
}
