import React, {useState} from "react";
import Grid from "@material-ui/core/Grid";
import {
    Card,
    makeStyles,
    CardHeader,
    CardContent,
    Typography, Link,
} from "@material-ui/core";
import logo from "../../assets/imgs/logo_ctes.png";
import {FormLogin} from "./components/FormLogin";
import {Fotter} from "../../components/gen/Fotter";
import {FormRegister} from "./components/FormRegister";

const styles = makeStyles({
    body: {
        minHeight: "80vh",
    },
    cardStyle: {
        background: "rgba(234, 234, 234, 0.25);",
        borderRadius: "10px",
    },
    linkAuth:{
        color:"blue",
        padding:"1rem 0rem 0rem 0rem",
        cursor:"pointer"
    }
});

export const Auth = () => {
    const classes = styles();

    const [auth, setAuth] = useState(true);

    const changeAuthValue =()=>setAuth(!auth);


    return (
        <>
            <Grid
                alignContent="center"
                alignItems="center"
                justify="center"
                container
                className={classes.body}
            >
                <Grid
                    container
                    alignContent="center"
                    alignItems="center"
                    justify="center"
                >
                    <Card className={classes.cardStyle}>
                        <CardHeader
                            avatar={
                                <Grid
                                    container
                                    alignContent="center"
                                    alignItems="center"
                                    justify="center"
                                >
                                    <img src={logo} alt="imagen login"/>
                                </Grid>
                            }
                            title={<Typography variant="h4">{(auth) ? "Iniciar sesion" : "Crear nuevo usuario"}</Typography>}
                        />
                        <CardContent>
                            {( auth ) ? <FormLogin/> : <FormRegister functionChangeView={changeAuthValue}/>}
                            <Grid container justify={"center"}  >
                                <Link onClick={()=>setAuth(!auth)} className={classes.linkAuth}>
                                    {( auth )? "Crear una cuenta" : "Iniciar sesion"}
                                </Link>
                            </Grid>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
            <Grid container alignContent={"center"} style={{
                position: "absolute",
                bottom: 0,
                left: 0,
                backgroundColor: "transparent",
                width: "100%",
                padding: '4rem 0rem 0rem 0rem',
                margin: '2rem 0rem 0rem 0rem'
            }}>
                <Fotter/>
            </Grid>
        </>
    );
};
