import React, { useState } from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import logoCTES from "../../../assets/imgs/logoCTESnavbar.svg";
import Grid from "@material-ui/core/Grid";
import { DrawerOption, Strings } from "../../../assets/app_const/AppConstants";

import { useDispatch, useSelector } from "react-redux";
import { IconButton, Tab, Tabs, useMediaQuery, useTheme } from "@material-ui/core";
import { AccountCircle } from "@material-ui/icons";
import { createActionDrawerOpen, createActionLogOut } from "../../../views/LoginView/reducer/actionReducer";
import { NavLink, useHistory } from "react-router-dom"
import { createActionCloseResult } from "../../../views/SearchView/functions/ActionSearch";
import { pathFront } from "../../../views/LoginView/app_const/AppConst";


export const Navbar = () => {
    const theme = useTheme();
    const matches = useMediaQuery(theme.breakpoints.up('md'));
    const { Token, FullName } = useSelector((state) => state.auth);

    const [valueTab, setValueTab] = useState(0);

    const dispatch = useDispatch()
    let history = useHistory();

    const { Rol } = useSelector((state) => state.auth);



    const handleOpenMenu = (event) => {
        dispatch(createActionDrawerOpen());
    };

    const navigatorPush = (path,index) => {
        setValueTab(index);
        if (path) {
            if (path === pathFront.searchProject) {
                dispatch(createActionCloseResult());
            }
            history.push(path);
        } else {
            setValueTab(0);
            dispatch(createActionLogOut());
            history.replace(pathFront.home);
        }
    }


    return (
        <>
            <AppBar
                position="sticky"
                style={{
                    background: "#000000",
                }}
            >
                <Toolbar>
                    <Grid container direction="row" justify="center" >
                        <Grid
                            container
                            item
                            xl={7}
                            justify={Token ? "flex-start" : "center"}
                        >
                            <NavLink to="/">
                                <img src={logoCTES} alt="logo" />

                            </NavLink>

                        </Grid>
                        <Grid
                            container
                            item
                            xl={7}
                            justify={Token ? "flex-start" : "center"}
                        >
                            <NavLink to="/" style={{ textDecoration: "inherit", color: "inherit" }}>
                                <Typography
                                    variant="h5"
                                    style={{ color: "#eaeaea" }}
                                >
                                    {Strings.title_app}
                                </Typography>
                            </NavLink>
                        </Grid>
                    </Grid>

                    {Token ? (

                        (!matches) ?
                            <IconButton aria-controls="simple-menu" edge="start" color="inherit" aria-label="menu"
                                aria-haspopup="true" onClick={handleOpenMenu}>
                                <AccountCircle /> {" " + FullName}
                            </IconButton> : <Tabs value={valueTab} aria-label="simple tabs example" variant="fullWidth" style={{ height: "60px", width: "200rem" }}>
                                {
                                    DrawerOption(Rol).map((item, index) => {
                                        return <Tab  label={item.text} style={{height:'60px'}} onClick={()=>navigatorPush(item.path,index) } />;
                                    })
                                }
                            </Tabs>

                    ) : <></>}



                </Toolbar>



            </AppBar>

        </>
    );
};
