import {
    getterRol,
    Info,
    localConfigure,
} from "../../../assets/app_const/AppConstants";
import {snackColors} from "../../../assets/app_const/ColorsConst";
import api from "../../../axios/API";
import {createActionActiveSnackbar} from "../../../reducer/actionUIReducer";
import {pathApi} from "../app_const/AppConst";
import {AuthType} from "./Types";
import {TypeUi} from "../../../reducer/TypesUi";

export const loginAction = (email, password) => {
    return async (dispatch) => {
        try {
            const resp = await api.post(pathApi.login, {
                email,
                password,
            });
            const {name, token, rol} = resp.data.data;
            localStorage.setItem(localConfigure.TOKENKEY, token);
            dispatch(
                createActionActiveSnackbar(Info.login_success, snackColors.success)
            );
            dispatch(createActionLogin(name, token, rol));
        } catch (err) {
            if (err.response) {
                dispatch(
                    createActionActiveSnackbar(err.response.data.msg, snackColors.error)
                );
            } else {
                dispatch(
                    createActionActiveSnackbar(Info.login_error, snackColors.error)
                );
            }
        }
    };
};

const createActionLogin = (name, token, rol) => {
    const nameRol = getterRol(rol);
    return {
        type: AuthType.login,
        payload: {
            FullName: name,
            Token: token,
            Rol: nameRol,
        },
    };
};

export const createActionLogOut = () => {
    return dispatch => {
        localStorage.removeItem(localConfigure.TOKENKEY);
        dispatch({
            type: AuthType.logout
        });
    }
}

export const createActionDrawerOpen = () => {
    return dispatch => {
        dispatch({
            type: TypeUi.openDrawer
        });
    }
}
export const createActionDrawerClose = () => {
    return dispatch => {
        dispatch({
            type: TypeUi.closeDrawer
        });
    }
}


export const confirmedAuthToken = (token) => {
    return async (dispatch) => {
        try {
            const resp = await api.get(pathApi.confirmedToken);
            const {fullName, rol} = resp.data.data;
            dispatch(createActionLogin(fullName, token, rol));
        } catch (error) {
            console.log(error);
        }
    };
};
