import axios from "axios";
import { localConfigure } from "../assets/app_const/AppConstants";

const api = axios.create({
  baseURL: process.env.REACT_APP_SERVER_URL,
  timeout: 5000,
  headers: { "Content-Type": "application/json" },
});
// solicitud de configuracin de solicitud
api.interceptors.request.use(
  (config) => {
    if (localStorage) {
      const token = localStorage.getItem(localConfigure.TOKENKEY);
      if (token) {
        config.headers["Authorization"] = "Bearer " + token;
      }
      return config;
    } else {
      Notification({
        title: "error",
        message:
          "La versin de su navegador es demasiado baja, actualice su navegador",
        type: "error",
      });
    }
  },
  (error) => {
    console.error(error);
    Notification({
      title: "error",
      message: "Solicitud fallida:" + error.message,
      type: "error",
    });
    Promise.reject(error);
  }
);

export default api;
