import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { createActionCloseSnackbar } from "../../reducer/actionUIReducer";
import Snackbar from "@material-ui/core/Snackbar";
import { makeStyles, SnackbarContent } from "@material-ui/core";

const useStyles = makeStyles({
  snack: {
    backgroundColor: (props) => props.color,
  },
});
export const SnackbarToast = () => {
  const dispatch = useDispatch();
  const { show, msg, color } = useSelector((state) => state.ui.Snackbar);
  const classes = useStyles({
    color: color,
  });

  const hanldeOnCloseSnackbar = () => {
    dispatch(createActionCloseSnackbar());
  };

  return (
    <div>
      {
        <Snackbar
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "left",
          }}
          open={show}
          autoHideDuration={2000}
          onClose={hanldeOnCloseSnackbar}
        >
          <SnackbarContent message={msg} className={classes.snack} />
        </Snackbar>
      }
    </div>
  );
};
