import { Button, Card, CardContent, CardHeader, Grid, makeStyles, Typography } from "@material-ui/core";
import { BackgroundColor, TextColor } from "../../../assets/app_const/ColorsConst";
import { DescriptionProject } from "./DescriptionProject";
import { Link } from "react-router-dom";
import { pathApi, pathFront } from "../../LoginView/app_const/AppConst";
import api from "../../../axios/API";
import { useSelector } from "react-redux";
import { informador } from "../../../assets/app_const/AppConstants";

const useStyle = makeStyles({
    CardStyle: {
        width: "100%",
        backgroundColor: BackgroundColor.cardProjectColor,
        padding: "1rem"
    },
    buttonStyle: {
        backgroundColor: BackgroundColor.primary_main,
        color: TextColor.textWhite
    },
    buttonUpdateStyle: {
        marginTop:"0.5rem",
        backgroundColor: BackgroundColor.primary_blue,
        color: TextColor.textWhite
    },buttonRejectStyle: {
        backgroundColor: BackgroundColor.primary_red,
        color: TextColor.textWhite
    },
    gridRoot: {
        padding: "1rem"
    }
})

export const CardProject = (project) => {
    const classes = useStyle();

    const {Rol} = useSelector(state => state.auth);

    const handlerActionEnable=async(id,request)=>{

        try {
            await api.put(pathApi.enableProject,{
                enable:request,
                idProject:id
            });

            project.setFilter([...project.filter,id]);
        } catch (e) {
            console.log(e);
        }

    }


    return <Grid container className={classes.gridRoot} justify={"center"} item xl={6} lg={6} md={12} sm={12} xs={12} key={project.id}>
        <Card className={classes.CardStyle} variant="outlined">
            <CardHeader
                title={<Typography variant={"body1"}><strong>PROYECTO </strong>{project.NameProyecto}</Typography>}
                subheader={<Typography variant={"body2"}>{project.Descripcion}</Typography>}
            />
            <CardContent>
                <Grid container justify={"center"}>
                    <Grid container item xl={9} lg={9} md={9} sm={12} xs={12}>
                        <DescriptionProject {...project} />
                    </Grid>
                    <Grid container item xl={3} lg={3} md={3} sm={12} xs={12} justify={"flex-end"}
                        alignContent={"flex-end"}>
                        <Grid container direction="column">
                            <Link to={pathFront.projetInfo + project.id}
                                style={{ textDecoration: "inherit", color: "inherit" }}>

                                <Button variant="contained" className={classes.buttonStyle}>
                                    Ver Detalle
                                </Button>
                            </Link>

                            {
                                (Rol===informador)&&<Link to={pathFront.updateProject + project.id}
                                style={{ textDecoration: "inherit", color: "inherit" }}>

                                <Button variant="contained" className={classes.buttonUpdateStyle} disabled={project.update}>
                                    Actualizar
                                </Button>
                            </Link>
                            }


                        </Grid>
                    </Grid>
                </Grid>
                {
                    (project.buttonEnabled) && <Grid container justify="center" style={{height:"100%",margin:"2rem"}}>

                        <Button variant="contained" className={classes.buttonStyle} onClick={()=>handlerActionEnable(project.id,true)} style={{marginRight:"0.5rem"}}>
                            Aprobar 
                        </Button>

                        <Button variant="contained" className={classes.buttonRejectStyle} onClick={()=>handlerActionEnable(project.id,false)}  style={{marginLeft:"0.5rem"}}>
                            Rechazar 
                        </Button>

                    </Grid>
                }
            </CardContent>
        </Card>
    </Grid>
}