import api from "../../../axios/API";
import {pathApi} from "../../LoginView/app_const/AppConst";
import {createActionActiveSnackbar} from "../../../reducer/actionUIReducer";
import {snackColors} from "../../../assets/app_const/ColorsConst";
import {Info} from "../../../assets/app_const/AppConstants";


export const getUsersPendingLoad = async (setUserPendings )=>{
    try{
        const resp = await api(pathApi.userPendings);
        setUserPendings({users: resp.data.data,loading:false});
    }catch (e) {
        setUserPendings({users:[],loading:false});
        console.log(e);
    }
}

export const setUserActive =  (id,condition)=>{
    return async (dispatch)=>{
        try{
            const resp = await api.put(pathApi.updateUsers,{
                "condition":condition,
                "idUserPut":id
            });
            dispatch(
                createActionActiveSnackbar(resp.data.msg, snackColors.success)
            );
        }catch (e) {
            console.log(e);
            if (e.response) {
                dispatch(
                    createActionActiveSnackbar(e.response.data.msg, snackColors.error)
                );
            } else {
                dispatch(
                    createActionActiveSnackbar(Info.register_error, snackColors.error)
                );
            }
        }
    }
}