import { Box, Grid, makeStyles, Typography } from '@material-ui/core';
import React, { useState } from 'react'
import { useEffect } from 'react';
import { useParams } from 'react-router-dom'
import { Info, TypesSearch } from '../../assets/app_const/AppConstants';
import { BackgroundColor } from '../../assets/app_const/ColorsConst';
import api from '../../axios/API';
import { EmptyData } from '../../components/gen/EmptyData';
import { Fotter } from '../../components/gen/Fotter';
import { CardProject } from '../HomeView/components_socios/CardProject';
import { pathApi } from '../LoginView/app_const/AppConst';



const useStyle = makeStyles({
    gridContainerProject: {
        padding: '2rem 2rem 2rem 2rem',
        minHeight: "76vh"
    }
});

export const SearchFilterView = ({ type }) => {
    const { id } = useParams();
    const classes = useStyle();

    const [ProyectPreview, setProyectPreview] = useState({ project: [], loading: true });


    useEffect(() => {
        let mounted = true;
        if (mounted) {
            const getData = async () => {
                if (type === TypesSearch.region) {
                    const resp = await api.get(pathApi.searchProjectForRegion + id);
                    setProyectPreview({ project: resp.data.data, loading: false });
                } else {
                    if (type === TypesSearch.stateProject) {
                        const resp = await api.get(pathApi.searchProjectForState + id);
                        setProyectPreview({ project: resp.data.data, loading: false });
                    }
                    else {
                        if (type === TypesSearch.clasification) {
                            const resp = await api.get(pathApi.searchProjectForClasification + id);
                            setProyectPreview({ project: resp.data.data, loading: false });
                        } else {
                            setProyectPreview({ project: [], loading: false });
                        }
                    }
                }
            }
            getData();
        }
        return () => {
            mounted = false;
        }
    }, [id, type])

    if (ProyectPreview.loading) return <>cargando...</>;

    if (ProyectPreview.project.length === 0) return <Grid container justify={"center"} alignContent={"center"}
        direction={"column"}> <EmptyData not_data={Info.not_data} />
    </Grid>;



    return (
        <>
            <Box p={2}>
                {
                    type === TypesSearch.region ?
                        <Typography variant="h4" align="center" > Busqueda por region  </Typography> :
                        (type === TypesSearch.clasification) ?
                            <Typography variant="h4" > Busqueda por clasificacin  </Typography> :
                            <Typography variant="h4" > Busqueda por estado  </Typography>
                }
            </Box>
            <Grid container justify={"center"} className={classes.gridContainerProject}>

                {
                    ProyectPreview.project.map(project => {
                        return <CardProject {...project} key={project.id} />
                    })
                }

            </Grid>
            <Grid container alignContent={"center"} style={{
                bottom: 0,
                left: 0,
                backgroundColor: BackgroundColor.primary_black,
                width: "100%",
                padding: '0rem 0rem 0rem 1rem',
                margin: '2rem 0rem 0rem 0rem'
            }}>
                <Fotter />
            </Grid>
        </>
    )
}
