import {Grid, makeStyles, Typography} from "@material-ui/core";
import React from "react";

  const useStyle =makeStyles({
      gridDescription:{
          margin:"0.5rem 0rem 0.5rem 0rem"
      }
  })
export const DescriptionProject = ({Estado, Sector, Region, Comuna}) => {
      const classes = useStyle();
    const arrayDescription = [
        {
            x: "Estado: ",
            y: Estado
        },
        {
            x: "Sector: ",
            y: Sector
        },
        {
            x: "Region: ",
            y: Region
        },
        {
            x: "Comuna: ",
            y: Comuna
        }

    ]
    return <Grid container justify={"flex-start"}>
        {
            arrayDescription.map((des,index) => {
                return <React.Fragment key={index}>
                    <Grid container item xl={3} lg={3} md={3} sm={5} xs={5}  className={classes.gridDescription}>
                        <Typography variant={"body1"}><strong>{des.x}</strong></Typography>
                    </Grid>
                    <Grid container item xl={9} lg={9} md={9} sm={7} xs={7} className={classes.gridDescription}>
                        <Typography variant={"body1"}>{des.y ?? "No se tiene informacin"}</Typography>
                    </Grid>
                </React.Fragment>
            })
        }
    </Grid>
}