import React, {useEffect, useState} from 'react';
import {Grid, makeStyles, Typography} from "@material-ui/core";
import {BackgroundColor} from "../../../assets/app_const/ColorsConst";
import {cantTotalLongitud, cantTotalProject} from "./FuncionesCharts";

const Styles = makeStyles({
    cards: {
        background: BackgroundColor.primary_blue,
        padding: "3rem",
    },
    cardsTotal: {
        padding: "1rem",
    }
})

export const CantidadTotalProject = () => {

    const classes = Styles();

    const [cant, setCant] = useState(0);

    const [longitud, setLongitud] = useState(0);


    useEffect(() => {
        let mounted = true;

        if (mounted) {
            cantTotalProject(setCant)
            cantTotalLongitud(setLongitud);
        }

        return () => {
            mounted = false;
        };
    }, [setCant]);


    function getCantTotalProject() {
        return <Grid container justify={"center"} item xl={6} lg={6} md={12} sm={12} className={classes.cardsTotal}>
            <Grid container className={classes.cards}>
                <Typography variant={"h3"} style={{color: "white"}}>Cantidad total de proyectos: {cant}</Typography>
            </Grid>
        </Grid>;
    }

    return <>
        {getCantTotalProject()}
        <Grid container justify={"center"} item xl={6} lg={6} md={12} sm={12} className={classes.cardsTotal}>
            <Grid container className={classes.cards}>
                <Typography variant={"h3"} style={{color: "white"}}>Kilometros
                    Construidos: {longitud.toString().replace(".", ",")} km</Typography>

            </Grid>

        </Grid>
    </>


}

