import api from "../../../axios/API";
import {pathApi} from "../../../views/LoginView/app_const/AppConst";


export const cantTotalProject=async(setCant)=>{
    try{
        const resp = await api(pathApi.cantidadProject);
        setCant(resp.data.data);
    }catch (e) {
        console.log(e);
    }
}
