export const getInfoStateProject=async(setInfo)=>{
    /*
    *
    * {
      "id": 1,
      "NameState": "EIADIA",
      "cantidad": "1"
    }
    *
    * */

    /*
    * name
    * value
    * */
    try{
        const resp = await api(pathApi.projectForState);
        setInfo(resp.data.data.map(e=>{
            return {
                id:e.id,
                name:e.NameState,
                value:parseInt(e.cantidad)
            }
        }).filter(e=>e.value!==0) );

    }catch (e) {
        console.log(e);
    }
}

