export const getInfoRegionProject=async(setInfo)=>{
    /*
    * {
      "id": 1,
      "NameState": "EIADIA",
      "cantidad": "1"
    }
    * */

    /*
    * name
    * size
    * */
    try{
        const resp = await api(pathApi.projectForRegion);
        let result = (resp.data.data.region.map(e=>{
            return {
                id:e.id,
                name:(e.NameRegion!==null) ? e.NameRegion +` (${e.cantidad})` : `Sin info. (${e.cantidad})`,
                size:parseInt(e.cantidad)
            }
        }).filter(e=>e.value!==0) );

        setInfo(result);

    }catch (e) {
        console.log(e);
    }
}