import {useParams} from "react-router-dom";
import React, {useEffect, useState} from "react";
import {getInfoProjectView} from "./functions";
import {Grid, makeStyles, Typography} from "@material-ui/core";
import {DetailProject} from "./DetailProject";
import {Caracteristicas} from "./Caracteristicas";
import {BackgroundColor} from "../../assets/app_const/ColorsConst";
import {Fotter} from "../../components/gen/Fotter";

const useStyle= makeStyles({
    root:{
        padding:"2rem"
    }
})

export const ProjectView = () => {
    const classes = useStyle();
    const {id} = useParams();

    const [Project, setProject] = useState(null);

    useEffect(() => {
        let mounted = true;
        if (mounted) {
            getInfoProjectView(id, setProject)
        }
        return () => {
            mounted = false;
        };
    }, [id]);

    if (!Project) return <>caragando..</>;
    return <>
        <Grid container justify={"center"} className={classes.root}>
        <Typography variant={"h1"} style={{padding:'4rem 0rem 4rem 0rem'}}>
          {"Proyecto "+Project.NameProyecto} 
      </Typography>
            <DetailProject Estado={Project.Estado} Clasificacion={Project.Clasificacion} Name={Project.NameProyecto}
                           Descripcion={Project.Descripcion} MetodoConstructivo={Project.MetodoConstructivo}
                           MontoInversion={Project.MontoInversion} TipoContrato={Project.TipoContrato}/>
            <Grid container>
                <Caracteristicas DateLicitacion={Project.FechaLicitacion} DateStart={Project.FechaInicioObras} Plazo={ Project.PlazoEjecucion}
                                 Long={Project.Longitud} Secc={Project.Seccion} Pendiente={Project.Pendiente}
                                 Region={Project.Region} Comuna={Project.Comuna} Oficinas={Project.DireccionOfIngenieria}
                />



            </Grid>
        </Grid>

        <Grid container alignContent={"center"} style={{
            bottom: 0,
            left: 0,
            backgroundColor: BackgroundColor.primary_black,
            width: "100%",
            padding: '1rem 0rem 1rem 1rem',
            margin: '4rem 0rem 0rem 0rem'
        }}>
            <Fotter/>
        </Grid>
    </>
}