import {Pagination} from "@material-ui/lab";


export const PaginationHandle=({cant,setPage} )=>{

    const changePage=(_,value)=>{
        setPage({
            cant:cant*8,
            pageNow:value,
        })
    }
    return <Pagination count={cant} variant="outlined" shape="rounded"
        onChange={changePage}
    />
}