const Styles = makeStyles({
    cardForm: {
        margin: "2rem 0rem 0rem 0rem",
        padding: "1rem 1rem 1rem 1rem",
        backgroundColor: "#eaeaea",
        textAlign: "center"
    },
    paddingTop: {
        margin: "1.5rem 0rem 0rem 0rem"
    },
    paddingBottom: {
        padding: "0rem 0rem 1rem 0rem"
    },
    buttonSearch: {
        backgroundColor: '#3A8B66',
        color: '#eaeaea',
        '&:hover': {
            backgroundColor: '#3A8B66',
        },
    },
    buttonClean: {
        backgroundColor: '#BB1C1A',
        color: '#eaeaea',
        '&:hover': {
            backgroundColor: '#BB1C1A',
        },

    }
})

export const FormSearch = () => {
    const initStateForm = {
        NombreProyecto: "",
        Estado: "",
        Sector: "",
        Region: "",
        Comuna: "",
        PlazoEjecucion: "",
        FechaAprobacion: {
            from: null,
            to: null
        },
        FechaIngreso: {
            from: null,
            to: null
        },
        NombreMandante: "",
        MontoProyecto: [0, 100],
        Longitud: [0, 5000],
    }

    const dispatch = useDispatch();

    const { form } = useSelector((state) => state.search);

    const [formSearch, setFormSearch] = useState(initStateForm);


    const [Regiones, setRegiones] = useState([]);

    const [Mandantes, setMandantes] = useState([]);

    const [Comuna, setComuna] = useState({comunas: [], active: false});

    useEffect(()=>{
        let mounted = true;
        const getRegiones=async()=>{
            const resp = await api.get(pathApi.regiones);
            setRegiones(resp.data.data);
        }
        if(mounted){
            getRegiones();
        }


        return ()=>{
            mounted=false;
        }
    },[]);

    useEffect(()=>{
        let mounted =true;

        if(mounted){
            setFormSearch(form);
        }

        return ()=>{
            mounted=false;
        }
    },[form]);

    useEffect(()=>{
        let mounted = true;
        const getMandantes=async()=>{
            const resp = await api.get(pathApi.mandantes);
            setMandantes(resp.data.data);
        }
        if(mounted){
            getMandantes();
        }

        return ()=>{
            mounted=false;
        }
    },[]);

    useEffect(()=>{
        let mounted = true;
        const getComunas=async()=>{
            const resp = await api.get(pathApi.comunas.replace("idRegion",formSearch.Region));
            setComuna({comunas: resp.data.data,active: true});
        }
        if(mounted && formSearch.Region!== '' && formSearch.Region!==null){
            getComunas();
        }

        return ()=>{
            mounted=false;
        }
    },[formSearch.Region]);

    const SeccionEstadoSector = () => <Grid container justify={"center"} spacing={2}>
        <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
            <FormControl variant="outlined" fullWidth={true}>
                <InputLabel id="EstadoLabel">Estado</InputLabel>
                <Select labelId={"EstadoLabel"} id={"Estado"} name={"Estado"} value={formSearch.Estado}
                        onChange={hanlderInputChangeString}>

                    {
                        EstadosProject.map(item => <MenuItem key={item.id.toString() + "Estados"}
                                                             value={item.name}>{item.name}</MenuItem>)
                    }

                </Select>
            </FormControl>
        </Grid>
        <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
            <FormControl variant="outlined" fullWidth={true}>
                <InputLabel id="SectorLabel">Sector</InputLabel>
                <Select labelId={"SectorLabel"} id={"Sector"} name={"Sector"} value={formSearch.Sector}
                        onChange={hanlderInputChangeString}>

                    {
                        SectoresProyectos.map(item => <MenuItem key={item.id.toString() + "Sectores"}
                                                                value={item.name}>{item.name}</MenuItem>)
                    }

                </Select>
            </FormControl>


        </Grid>
    </Grid>

    const SeccionRegion = () => <Grid container justify={"center"} spacing={2}>
        <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
            <FormControl variant="outlined" fullWidth={true}>
                <InputLabel id="RegionLabel">Region</InputLabel>
                <Select labelId={"RegionLabel"} id={"Region"} name={"Region"} value={formSearch.Region}
                        onChange={hanlderInputChangeString}>

                    {
                        Regiones.map(item => <MenuItem key={item.id.toString() + "Region"}
                                                             value={item.id}>{item.NameRegion}</MenuItem>)
                    }

                </Select>
            </FormControl>
        </Grid>
        <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
            <FormControl variant="outlined" fullWidth={true}>
                <InputLabel id="ComunaLabel">Comuna</InputLabel>
                <Select labelId={"ComunaLabel"} id={"Comuna"} name={"Comuna"} value={formSearch.Comuna}
                        onChange={hanlderInputChangeString}
                        disabled={!Comuna.active}
                >

                    {
                        Comuna.comunas.map(item => <MenuItem key={item.id.toString() + "Comuna"}
                                                                value={item.id}>{item.NameComuna}</MenuItem>)
                    }

                </Select>
            </FormControl>


        </Grid>
    </Grid>



    /*const SeccionFechaAprobacion = () => <Grid container justify={"center"} spacing={2}>

        <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
            <TextField
                id="FechaAprobacion-from"
                label="desde"
                type="date"
                value={Moment(formSearch.FechaAprobacion.from ?? new Date()).format('YYYY-MM-DD')}

                InputLabelProps={{
                    shrink: true,
                }}
                fullWidth={true}
                variant={"outlined"}
                onChange={handlerInputDates}

            />
        </Grid>
        <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
            <TextField
                id="FechaAprobacion-to"
                label="hasta"
                type="date"
                value={Moment(formSearch.FechaAprobacion.to ?? new Date()).format('YYYY-MM-DD')}
                InputLabelProps={{
                    shrink: true,
                }}
                fullWidth={true}
                variant={"outlined"}
                onChange={handlerInputDates}

            />
        </Grid>
    </Grid>*/

    /*const SeccionFechaIngreso = () => <Grid container justify={"center"} spacing={2}>

        <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
            <TextField
                id="FechaIngreso-from"
                label="desde"
                type="date"
                InputLabelProps={{
                    shrink: true,
                }}
                value={Moment(formSearch.FechaIngreso.from ?? new Date()).format('YYYY-MM-DD')}
                fullWidth={true}
                variant={"outlined"}
                onChange={handlerInputDates}
            />
        </Grid>
        <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
            <TextField
                id="FechaIngreso-to"
                label="hasta"
                type="date"
                InputLabelProps={{
                    shrink: true,
                }}
                value={Moment(formSearch.FechaIngreso.to ?? new Date()).format('YYYY-MM-DD')}
                fullWidth={true}
                variant={"outlined"}
                onChange={handlerInputDates}

            />
        </Grid>
    </Grid>*/


    const hanlderInputChangeString = (e) => {

        setFormSearch(
            {
                ...formSearch,
                [e.target.id ?? e.target.name]: e.target.value ?? e.target.value
            }
        )
    }

    const hanlderChangeRangeMontos = (e, newVal) => {
        setFormSearch({
            ...formSearch,
            [e.target.id]: newVal
        });
    }

    const handlerSubmitFormSearch = (e) => {
        e.preventDefault();
        if (formSearch === initStateForm) {

            dispatch(
                createActionActiveSnackbar(Info.not_modify_form, snackColors.error)
            );
            return;
        }
        dispatch(searchActionSubmit(1, formSearch));

    }

    /*const handlerInputDates = (e) => {
        //(e.target.value) valor
        let attributesObjects = e.target.id.split("-");
        setFormSearch({
            ...formSearch,
            [attributesObjects[0]]: {
                ...formSearch[attributesObjects[0]],
                [attributesObjects[1]]: e.target.value
            }
        });
    }*/

    const hanlderCleanInputs=() => {
        dispatch(createActionCleanInput());
        setFormSearch(initStateForm);
    }

    const classes = Styles();
    return <>
        <Card className={classes.cardForm}>
            <form onSubmit={handlerSubmitFormSearch}>
                <TextField
                    onChange={hanlderInputChangeString}
                    variant={"outlined"}
                    value={formSearch.NombreProyecto}
                    id={'NombreProyecto'}
                    fullWidth={true}
                    label={"Nombre del proyecto"}
                />
                <SeccionEstadoSector/>
                <SeccionRegion />

                <Grid container className={classes.paddingTop}>
                    <FormControl variant="outlined" fullWidth={true}>
                        <InputLabel id="NombreMandanteLabel">Nombre del mandante</InputLabel>
                        <Select labelId={"NombreMandanteLabel"} id={"NombreMandante"} name={"NombreMandante"} value={formSearch.NombreMandante}
                                onChange={hanlderInputChangeString}>

                            {
                                Mandantes.map(item => <MenuItem key={item.id.toString() + "Region"}
                                                               value={item.id}>{item.FullName}</MenuItem>)
                            }

                        </Select>
                    </FormControl>
                </Grid>
                <Typography
                    id="MontoProyectoLabel"
                    gutterBottom
                    className={classes.paddingTop}
                >
                    Monto del Proyecto (MM)
                </Typography>
                <Slider
                    value={formSearch.MontoProyecto}
                    onChangeCommitted={hanlderChangeRangeMontos}
                    valueLabelDisplay="on"
                    aria-labelledby="MontoProyectoLabel"
                    id={"MontoProyecto"}
                />

                <Grid
                    container
                    justify={"center"}
                    spacing={1}
                    className={classes.paddingTop}
                >
                    <Grid item container justify={"flex-end"} xl={6} lg={6} md={6} xs={6} sm={6}>
                        <Button type={"submit"} className={classes.buttonSearch} disableTouchRipple={true}>
                            Buscar
                        </Button>
                    </Grid>
                    <Grid item container xl={6} lg={6} md={6} xs={6} sm={6}>
                        <Button onClick={hanlderCleanInputs} className={classes.buttonClean}>
                            Limpiar
                        </Button>
                    </Grid>
                </Grid>
            </form>
        </Card>
    </>


}

