import api from "../../../axios/API";
import {pathApi} from "../app_const/AppConst";
import {Info} from "../../../assets/app_const/AppConstants";
import {createActionActiveSnackbar} from "../../../reducer/actionUIReducer";
import {snackColors} from "../../../assets/app_const/ColorsConst";

export const registerAction = (email, password,fullName) => {
    return async (dispatch) => {
        try {
            const resp = await api.post(pathApi.register, {
                email,
                password,
                fullName
            });
            const {msg} = resp.data;
            dispatch(
                createActionActiveSnackbar(msg, snackColors.success)
            );
        } catch (err) {
            if (err.response) {
                dispatch(
                    createActionActiveSnackbar(err.response.data.msg, snackColors.error)
                );
            } else {
                dispatch(
                    createActionActiveSnackbar(Info.register_error, snackColors.error)
                );
            }
        }
    };
};
