import { Grid, makeStyles, Typography } from '@material-ui/core'
import React from 'react'
import { useHistory } from 'react-router-dom';
import { HomeOption } from '../../assets/app_const/AppConstants';


const useStyle = makeStyles({
    cardContainer: {
        border: "0.5px solid",
        padding: '4rem 0rem 4rem 0rem',
        margin: '2rem 2rem 2rem 2rem',
        boxShadow: "0.5px 0.5px 5px 0px #111111",
        borderRadius: "0.9em",
        backgroundColor: "#eaeaea",
        cursor: "pointer"
    },
    bodyItem: {
        fontWeight: 'inherit',
        padding: '2rem 2rem 2rem 2rem',
        textAlign: "center"
    }
})

export const ResumenAdmin = () => {
    const classes = useStyle();
    const history = useHistory();
    const {admin_menu} = HomeOption;

    const hanlderClickItem=(path)=>history.push(path)
    return (
        <>
        <Typography variant={"h3"} align="center">
            Resumen
        </Typography>

        <Grid container justify={"center"}>
            {
                admin_menu.map((item) =>
                    <Grid item container justify={"center"} xl={6} lg={6} md={6} sm={12} xs={12} key={item.title} >
                        <Grid container onClick={()=>hanlderClickItem(item.path)} className={classes.cardContainer} direction={"column"} alignContent={"center"}
                              alignItems={"center"}>
                            {item.iconOption}
                            <Typography variant={"h3"} className={classes.bodyItem}>
                                {item.title}
                            </Typography>
                        </Grid>
                    </Grid>)
            }

        </Grid>
        </>
    )
}
