import api from "../../../axios/API";
import {pathApi} from "../../LoginView/app_const/AppConst";
import {TypeSearch} from "../reducer/TypeSearch";


export const searchActionSubmit = (page = 1, form) => {
    const size = 4;
    const skip = (page - 1) * 4;
    return async (dispatch) => {
        try {
            const resp = await api.post(pathApi.previwProject + `?skip=${skip}&size=${size}`,
                {
                    params: form
                }
            );
            dispatch(createActionAddResult(resp.data.data));
            console.log(resp.data.data);
            if(resp.data.data.length>0){
                dispatch(createActionOpenResult());
            }
            dispatch(createActionAddForm(form));
        } catch (e) {
            console.log(e);
        }
    }

}


export const createActionAddResult=(resultado)=>{
    return {
        type:TypeSearch.AddResult,
        payload:{
            res:resultado
        }
    }
}

export const createActionAddForm = (form) => {

    return {
        type: TypeSearch.AddSearch,
        payload: {
            form: form
        }
    }
}

export const createActionCleanInput=()=>{
    return{
        type:TypeSearch.DeleteSearch
    }
}

export const createActionOpenResult=()=>{

    return{
        type:TypeSearch.ViewResult
    }
}
export const createActionCloseResult=()=>{
    return{
        type:TypeSearch.CloseResult
    }
}