import { Grid, makeStyles, Typography } from '@material-ui/core';

import React, { useEffect, useState } from 'react'
import { Info } from '../../assets/app_const/AppConstants';
import api from '../../axios/API';
import { EmptyData } from '../../components/gen/EmptyData';
import { CardProject } from '../HomeView/components_socios/CardProject';
import { pathApi } from '../LoginView/app_const/AppConst';

const useStyle = makeStyles({
    gridContainerProject: {
        padding: '2rem 2rem 2rem 2rem',
        minHeight: "76vh"
    }
})
export const InformadorProjectPending = () => {
    const classes = useStyle();
    const [ProyectPreview, setProyectPreview] = useState({ project: [], loading: true });

    useEffect(() => {
        let mounted = true;

        const getData = async () => {
            try {
                const resp = await api.get(pathApi.pendingProjectId);
                setProyectPreview({
                    project: resp.data.data,
                    loading: false
                });
            } catch (error) {
                console.log(error);
                setProyectPreview({
                    project: [],
                    loading: false
                });
            }
        }
        if (mounted) {
            getData();
        }

        return () => {
            mounted = false;
        }
    }, [])

    if (ProyectPreview.loading) return <>cargando...</>;

    if (ProyectPreview.project.length === 0) return <Grid container justify={"center"} alignContent={"center"}
        direction={"column"}> <EmptyData not_data={Info.not_data} />
    </Grid>;

    return <>
        <Grid container justify="center" style={{ paddingTop: "1rem" }}>
            <Typography variant="h2"> Proyectos pendiente revision </Typography>
        </Grid>
        <Grid container justify={"center"} className={classes.gridContainerProject}>

            {
                ProyectPreview.project.map(project => {
                    return <CardProject {...project} key={project.id} update={true} />
                })
            }
        </Grid>


    </>
}
