import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import { Navbar } from "../components/gen/nav/Navbar";
import { Auth } from "../views/LoginView/Auth";
import { confirmedToken } from "./functionRouter";
import { RouterPrivate } from "./RouterPrivate";
import { ProjectView } from "../views/ProjectView/ProjectView";
import { pathFront } from "../views/LoginView/app_const/AppConst";
import { ReturnHomeForRol } from "./ReturnHomeForRol";
import { localConfigure, TypesSearch } from "../assets/app_const/AppConstants";
import { RequestSignIn } from "../views/ValidadorViews/RequestSignIn";
import { SearchView } from "../views/SearchView/SearchView";
import { DrawerUi } from "../components/gen/nav/DrawerUi";
import { FormAddProject } from "../views/InformadorView/components/FormAddProjectView";
import { ProjectPendingView } from "../views/ValidadorViews/ProjectPendingView";
import { ValidadorProjectView } from "../views/ValidadorViews/ValidadorProjectView";
import { InformadorProjectReject } from "../views/InformadorView/InformadorProjectReject";
import { InformadorProjectPending } from "../views/InformadorView/InformadorProjectPending";
import { ProjectPendingUpdateView } from "../views/ValidadorViews/ProjectPendingUpdateView";
import { InformadorUpdateProject } from "../views/InformadorView/InformadorUpdateProject";
import { AdminUsers } from "../views/AdminView/AdminUsers";
import { SearchFilterView } from "../views/SearchFilterView/SearchFilterView";


export const Routers = () => {
    const { Token } = useSelector((state) => state.auth);
    const dispatch = useDispatch();

    if (!Token) {
        confirmedToken(dispatch);
    }
    return (
        <>

            <Router>
                <DrawerUi />
                <Navbar />
                <Switch>
                    {!Token ?
                        <Route path="/">
                            <Auth />
                        </Route>
                        : <RouterPrivate privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                            path={"/"} component={ReturnHomeForRol} />
                    }

                    <RouterPrivate privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.requestSignIn} component={RequestSignIn} />

                    <RouterPrivate privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.projetInfo + ":id"} component={ProjectView} />

                    <RouterPrivate privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.searchProject} component={SearchView} />

                    <RouterPrivate privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.addProject} component={FormAddProject} />

                    <RouterPrivate privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.projectPending} component={ProjectPendingView} />

                    <RouterPrivate privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.projectActive} component={ValidadorProjectView} />

                    <RouterPrivate privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.projectReject} component={InformadorProjectReject} />

                    <RouterPrivate privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.prjectPendingInformante} component={InformadorProjectPending} />

                    <RouterPrivate privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.prjectPendingUpdate} component={ProjectPendingUpdateView} />

                    <RouterPrivate privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.updateProject + ":id"} component={InformadorUpdateProject} />

                    <RouterPrivate privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.userAdmin} component={AdminUsers} />

                    <RouterPrivate type={TypesSearch.region} privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.searchProjectRegion+":id"} component={SearchFilterView} />

                    <RouterPrivate type={TypesSearch.stateProject} privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.searchProjectState+":id"} component={SearchFilterView} />

                    <RouterPrivate type={TypesSearch.clasification} privateRoute={(localStorage.getItem(localConfigure.TOKENKEY)) ? true : false} exact
                        path={pathFront.searchProjectClasification+":id"} component={SearchFilterView} />

                    <Redirect to={"/"} />

                </Switch>

            </Router>


        </>
    );
};
