import {Grid, makeStyles, Typography} from "@material-ui/core";
import { TextColor} from "../../assets/app_const/ColorsConst";
import {ParamsProject} from "./ParamsProject";
import React from "react";

const useStyle=makeStyles({
    textDesctiption:{
        fontStyle:"italic",
        color:TextColor.TextDisable,
        fontWeight: 200,
        fontSize:20
    }
})

export const DetailProject=({Descripcion, Estado, Clasificacion, MontoInversion, MetodoConstructivo,TipoContrato})=>{
    const classes = useStyle();
    const arrayDetails = [

        {
            x:"Estado: ",
            y:Estado
        },
        {
            x:"Clasificacion: ",
            y:Clasificacion
        },
        {
            x:"Monto Inversion: ",
            y:MontoInversion
        },
        {
            x:"Metodo Constructivo: ",
            y:MetodoConstructivo
        },
        {
            x:"TipoContrato: ",
            y:TipoContrato
        },
    ]
  return <Grid container justify={"center"} alignContent={"center"} direction={"column"} spacing={2} style={{minHeight:"vh"}} >
      
      <Typography variant={"body1"} className={classes.textDescription}>
          {Descripcion}
      </Typography>

      <Grid container justify={"center"} alignContent={"center"} >
          {
              arrayDetails.map((param,i)=><ParamsProject key={"param"+i} {...param} />)
          }
      </Grid>



  </Grid>
}