import {createStore, combineReducers, applyMiddleware, compose} from "redux";
import {AuthReducer} from "../views/LoginView/reducer/authReducer";
import {UiReducer} from "../reducer/uiReducer";
import thunk from "redux-thunk";
import {SearchReducer} from "../views/SearchView/reducer/searchReducer";

const reducers = combineReducers({
    auth: AuthReducer,
    ui: UiReducer,
    search: SearchReducer
});

const composeEnhancers =
    typeof window === "object" && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
        ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({
            // Specify extensions options like name, actionsBlacklist, actionsCreators, serialize...
        })
        : compose;

export const store = createStore(
    reducers,
    composeEnhancers(applyMiddleware(thunk))
);
