import React, {useEffect, useState} from "react";
import {loadPageCant, ReturnProjectPreview} from "../functions/FunctionProyects";
import {Grid, makeStyles} from "@material-ui/core";

import {CardProject} from "./CardProject";
import {PaginationHandle} from "../../../components/gen/Pagination";
import {EmptyData} from "../../../components/gen/EmptyData";
import {Info} from "../../../assets/app_const/AppConstants";




const useStyle = makeStyles({
    gridContainerProject: {
        padding: '2rem 2rem 2rem 2rem',
        minHeight: "76vh"
    }
})
export const RecentlyHome = () => {
    const classes = useStyle();

    const [Page, setPage] = useState({
        pageNow: 1,
        cant: null
    });
    const [ProyectPreview, setProyectPreview] = useState({project: [], loading: true});

    useEffect(() => {
        let mounted = true;
        const getInfo=async ()=>{
            await ReturnProjectPreview(Page.pageNow, setProyectPreview);

        }
        if (mounted) {
            if (Page.cant !== null) {
                getInfo();
            }
            if (Page.cant == null) {
                loadPageCant(setPage);
            }

        }

        return () => {
            mounted = false;
        };
    }, [Page]);


    if (ProyectPreview.loading) return <>cargando...</>;

    if (ProyectPreview.project.length === 0) return <Grid container justify={"center"} alignContent={"center"}
                                                          direction={"column"}> <EmptyData not_data={Info.not_data}/>
    </Grid>;

    return <>
        <Grid container justify={"center"}  className={classes.gridContainerProject}>

            {
                ProyectPreview.project.map(project => {
                    return <CardProject {...project} key={project.id}/>
                })
            }
            <Grid container><PaginationHandle cant={Math.ceil(Page.cant / 8)} setPage={setPage}/></Grid>
        </Grid>
        


    </>

}