import api from "../../axios/API";
import {pathApi} from "../LoginView/app_const/AppConst";


export const getInfoProjectView=async(id,setProject)=>{
    try{
        const resp = await api(pathApi.infoProject+id);
        setProject(resp.data.data);
    }catch (e) {
        console.log(e)
    }

}

export const getFormUpdateProjectView=async(id,setProject)=>{
    try{
        const resp = await api(pathApi.infoUpdateProject+id);
        setProject(resp.data.data);
    }catch (e) {
        console.log(e)
    }

}