

import { Button, FormControl, Grid, InputLabel, Link, makeStyles, MenuItem, Select, TextField, Typography } from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { EstadosProject, Info, SectoresProyectos } from '../../../assets/app_const/AppConstants';
import { snackColors } from '../../../assets/app_const/ColorsConst';
import api from '../../../axios/API';
import { createActionActiveSnackbar } from '../../../reducer/actionUIReducer';
import { pathApi } from '../../LoginView/app_const/AppConst';

const Styles = makeStyles({
    cardForm: {
        margin: "2rem 0rem 0rem 0rem",
        padding: "1rem 1rem 1rem 1rem",
        backgroundColor: "#eaeaea",
        textAlign: "center"
    },
    paddingTop: {
        margin: "1.5rem 0rem 0rem 0rem"
    },
    paddingBottom: {
        padding: "0rem 0rem 1rem 0rem"
    },
    buttonSearch: {
        backgroundColor: '#3A8B66',
        color: '#eaeaea',
        '&:hover': {
            backgroundColor: '#3A8B66',
        },
    },
    buttonClean: {
        backgroundColor: '#BB1C1A',
        color: '#eaeaea',
        '&:hover': {
            backgroundColor: '#BB1C1A',
        },

    }
})

export const FormProjecto = ({form=null, update=false , id = null}) => {

    const classes = Styles();
    const initStateForm = (form) ? form : {
        NombreProyecto: "",
        Estado: null,
        Sector: null,
        Region: null,
        Comuna: null,
        PlazoEjecucion: null,
        FechaLicitacion: null,
        FechaInicioObras: null,
        NombreMandante: null,
        MontoProyecto: null,
        Longitud: null,
        Seccion: null,
        Pendiente: null,
        MetodoConstructivo: null,
        Oficinas: null
    }
    const [FormProject, setFormProject] = useState(initStateForm);
    const [Regiones, setRegiones] = useState([]);
    const [Comuna, setComuna] = useState({ comunas: [], active: false });
    const [Mandantes, setMandantes] = useState([]);
    const [MandanteInput, setMandanteInput] = useState(true);

    useEffect(() => {
        let mounted = true;
        const getRegiones = async () => {
            const resp = await api.get(pathApi.regiones);
            setRegiones(resp.data.data);
        }
        if (mounted) {
            getRegiones();
        }


        return () => {
            mounted = false;
        }
    }, []);

    useEffect(() => {
        let mounted = true;
        const getComunas = async () => {
            const resp = await api.get(pathApi.comunas.replace("idRegion", FormProject.Region));
            setComuna({ comunas: resp.data.data, active: true });
        }
        if (mounted && FormProject.Region !== '' && FormProject.Region !== null) {
            getComunas();
        }

        return () => {
            mounted = false;
        }
    }, [FormProject.Region]);

    useEffect(() => {
        let mounted = true;
        const getMandantes = async () => {
            const resp = await api.get(pathApi.mandantes);
            setMandantes(resp.data.data);
        }
        if (mounted) {
            getMandantes();
        }

        return () => {
            mounted = false;
        }
    }, []);

    const hanlderInputChangeString = (e) => {

        setFormProject(
            {
                ...FormProject,
                [e.target.id ?? e.target.name]: e.target.value
            }
        )
    }

    let history = useHistory();

    const dispatch = useDispatch();

    const hanlderSubmitForm = async(e) => {
        e.preventDefault();

        try {
            const resp = await api.post(pathApi.addProject,{
                form:FormProject
            });
            dispatch(
                createActionActiveSnackbar(resp.data.msg, snackColors.success)
            );
            history.push("/");
        } catch (err) {
            dispatch(
                createActionActiveSnackbar(err.response.data.msg ?? Info.register_error, snackColors.error)
            );
        }
    }
    const hanlderSubmitUpdateForm = async(e) => {
        e.preventDefault();

        try {
            const resp = await api.put(pathApi.updateProjectSend+id.toString(),{
                form:FormProject
            });
            dispatch(
                createActionActiveSnackbar(resp.data.msg, snackColors.success)
            );
            history.push("/");
        } catch (err) {
            dispatch(
                createActionActiveSnackbar(err.response.data.msg ?? Info.register_error, snackColors.error)
            );
        }
    }







    return (
        <Grid container className={classes.paddingTop}>
            <form style={{ width: "100%" }} onSubmit={ (update) ? hanlderSubmitUpdateForm : hanlderSubmitForm}>
                <TextField
                    className={classes.paddingBottom}
                    onChange={hanlderInputChangeString}
                    variant={"outlined"}
                    value={FormProject.NombreProyecto}
                    id={'NombreProyecto'}
                    fullWidth={true}
                    label={"Nombre del proyecto"}
                    required
                />
                <Grid container justify={"center"} spacing={2} >
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} >
                        <FormControl variant="outlined" fullWidth={true}>
                            <InputLabel id="EstadoLabel">Estado</InputLabel>
                            <Select labelId={"EstadoLabel"} id={"Estado"} name={"Estado"} value={FormProject.Estado}
                                onChange={hanlderInputChangeString}>

                                {
                                    EstadosProject.map(item => <MenuItem key={item.id.toString() + "Estados"}
                                        value={item.id}>{item.name}</MenuItem>)
                                }

                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} >
                        <FormControl variant="outlined" fullWidth={true}>
                            <InputLabel id="SectorLabel">Sector</InputLabel>
                            <Select labelId={"SectorLabel"} id={"Sector"} name={"Sector"} value={FormProject.Sector}
                                onChange={hanlderInputChangeString}>

                                {
                                    SectoresProyectos.map(item => <MenuItem key={item.id.toString() + "Sectores"}
                                        value={item.id}>{item.name}</MenuItem>)
                                }

                            </Select>
                        </FormControl>


                    </Grid>
                </Grid>

                <Grid container justify={"center"} spacing={2}>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
                        <FormControl variant="outlined" fullWidth={true}>
                            <InputLabel id="RegionLabel">Region</InputLabel>
                            <Select labelId={"RegionLabel"} id={"Region"} name={"Region"} value={FormProject.Region}
                                onChange={hanlderInputChangeString}>

                                {
                                    Regiones.map(item => <MenuItem key={item.id.toString() + "Region"}
                                        value={item.id}>{item.NameRegion}</MenuItem>)
                                }

                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
                        <FormControl variant="outlined" fullWidth={true}>
                            <InputLabel id="ComunaLabel">Comuna</InputLabel>
                            <Select labelId={"ComunaLabel"} id={"Comuna"} name={"Comuna"} value={FormProject.Comuna}
                                onChange={hanlderInputChangeString}
                                disabled={!Comuna.active}
                            >

                                {
                                    Comuna.comunas.map(item => <MenuItem key={item.id.toString() + "Comuna"}
                                        value={item.id}>{item.NameComuna}</MenuItem>)
                                }

                            </Select>
                        </FormControl>


                    </Grid>
                </Grid>

                <Grid container justify="center" spacing={2}>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
                        <TextField
                            id="PlazoEjecucion"
                            label="Plazo de ejecucion ( CANT. DE SEMESTRES )"
                            variant="outlined"
                            type="number"
                            fullWidth={true}
                            value={FormProject.PlazoEjecucion}
                            InputLabelProps={{
                                shrink: true,
                            }}
                            onChange={hanlderInputChangeString}
                        />
                    </Grid>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
                        <TextField
                            id="FechaLicitacion"
                            label="Fecha de licitacin"
                            variant="outlined"
                            type="date"
                            fullWidth={true}
                            value={FormProject.FechaLicitacion}
                            InputLabelProps={{
                                shrink: true,
                            }}
                            onChange={hanlderInputChangeString}
                        />
                    </Grid>
                </Grid>
                <Grid container justify="center" spacing={2}>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
                        <TextField
                            id="FechaInicioObras"
                            label="Fecha de inicio de obras"
                            variant="outlined"
                            type="date"
                            fullWidth={true}
                            value={FormProject.FechaInicioObras}
                            InputLabelProps={{
                                shrink: true,
                            }}
                            onChange={hanlderInputChangeString}
                        />
                    </Grid>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
                        {(MandanteInput) ? <FormControl variant="outlined" fullWidth={true}>
                            <InputLabel id="NombreMandanteLabel">Nombre del mandante</InputLabel>
                            <Select labelId={"NombreMandanteLabel"} id={"NombreMandante"} name={"NombreMandante"} value={FormProject.NombreMandante}
                                onChange={hanlderInputChangeString}>

                                {
                                    Mandantes.map(item => <MenuItem key={item.id.toString() + "Region"}
                                        value={item.id}>{item.FullName}</MenuItem>)
                                }

                            </Select>
                        </FormControl> :
                            <TextField
                                onChange={hanlderInputChangeString}
                                variant={"outlined"}
                                value={FormProject.NombreMandante}
                                id={'NombreMandante'}
                                fullWidth={true}
                                label={"Nombre del mandante"}
                            />}
                        <Link onClick={() => setMandanteInput(!MandanteInput)} >
                            <Typography variant="body1"> {
                                (MandanteInput) ?
                                    "Agregar nuevo mandante" :
                                    "Elegir entre los existentes"} </Typography>
                        </Link>
                    </Grid>
                </Grid>
                <Grid container justify="center" spacing={2}>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
                        <TextField
                            id="MontoProyecto"
                            label="Monto del Proyecto (MM)"
                            variant="outlined"
                            type="number"
                            fullWidth={true}
                            value={FormProject.MontoProyecto}
                            onChange={hanlderInputChangeString}
                        />
                    </Grid>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
                        <TextField
                            id="Longitud"
                            label="Longitud"
                            variant="outlined"
                            type="number"
                            fullWidth={true}
                            value={FormProject.Longitud}

                            onChange={hanlderInputChangeString}
                        />
                    </Grid>
                </Grid>
                <Grid container justify="center" spacing={2}>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
                        <TextField
                            id="Seccion"
                            label="Seccion"
                            variant="outlined"
                            type="text"
                            fullWidth={true}
                            value={FormProject.Seccion}
                            onChange={hanlderInputChangeString}
                        />
                    </Grid>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
                        <TextField
                            id="Pendiente"
                            label="Pendiente"
                            variant="outlined"
                            type="text"
                            fullWidth={true}
                            value={FormProject.Pendiente}

                            onChange={hanlderInputChangeString}
                        />
                    </Grid>
                </Grid>
                <Grid container justify="center" spacing={2}>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
                        <TextField
                            id="Oficinas"
                            label="Direccion de las oficinas de ingenieria"
                            variant="outlined"
                            type="text"
                            fullWidth={true}
                            value={FormProject.Oficinas}
                            onChange={hanlderInputChangeString}
                        />
                    </Grid>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6} className={classes.paddingTop}>
                        <TextField
                            id={"MetodoConstructivo"}
                            onChange={hanlderInputChangeString}
                            label={"Metodo Constructivo"}
                            type={"string"}
                            variant="outlined"
                            fullWidth={true}
                            value={FormProject.MetodoConstructivo}
                        />
                    </Grid>
                </Grid>
                <Grid
                    container
                    justify={"center"}
                    spacing={1}
                    className={classes.paddingTop}
                >
                    <Grid item container justify={"flex-end"} xl={6} lg={6} md={6} xs={6} sm={6}>
                        <Button type={"submit"} className={classes.buttonSearch} disableTouchRipple={true}>
                            {update ? "Actualizar" : "Enviar Proyecto"}
                        </Button>
                    </Grid>
                    <Grid item container xl={6} lg={6} md={6} xs={6} sm={6}>
                        <Button className={classes.buttonClean}>
                            Limpiar
                        </Button>
                    </Grid>
                </Grid>
            </form>
        </Grid>

    )
}
