import { TypeUi } from "./TypesUi";
export const createActionCloseSnackbar = () => {
  return {
    type: TypeUi.desactiveSnack,
  };
};

export const createActionActiveSnackbar = (msg, color) => {
  return {
    type: TypeUi.activateSnack,
    payload: {
      msg: msg,
      color: color,
    },
  };
};


