import { FormControl, Grid, InputLabel, MenuItem, Select, Typography } from '@material-ui/core'
import React, { useState } from 'react'
import { useEffect } from 'react';
import { rolesConst } from '../../assets/app_const/AppConstants';
import api from '../../axios/API';
import { pathApi } from '../LoginView/app_const/AppConst';

export const AdminUsers = () => {


  const [UserActive, setUserActive] = useState([]);

  useEffect(() => {
    let mounted = true;

    const getData = async () => {
      const resp = await api.get(pathApi.usersActivesAdmin);
      setUserActive(resp.data.data);

    }
    if (mounted) {
      getData();
    }
    return () => {
      mounted = false;
    }
  }, []);


  //{  idUserPut, rol }
  const handlerChangeRolUser = async (e, id) => {
    await api.put(pathApi.userUpdateRol,{
      idUserPut:id,
      rol:e
    });
    const resp = await api.get(pathApi.usersActivesAdmin);
    setUserActive(resp.data.data);

  }

  return (
    <>
      <Grid container justify="center">

        <Typography variant="h3"> Administracion de usuarios </Typography>


        <Grid container justify="center" style={{ margin: "2rem 0rem 0rem 0rem", padding: "1rem", background: "#eaeaea" }}>
          <Grid container item justify="center" xs={7} sm={7} md={5} lg={5} xl={5} >

            <Typography variant="h4"> Nombre de usuario </Typography>



          </Grid>
          <Grid container item justify="center" xs={3} sm={3} md={3} lg={3} xl={3} >

            <Typography variant="h4"> Rol </Typography>

          </Grid>
        </Grid>

        <Grid container justify="center" style={{ margin: "2rem 0rem 0rem 0rem", padding: "2rem", background: "#eaeaea" }}>
          {
            UserActive.map(u => <React.Fragment>
              <Grid container item justify="center" style={{ padding: "1rem" }} xs={7} sm={7} md={5} lg={5} xl={5} >

                <Typography variant="h5"> {u.FullName} </Typography>



              </Grid>
              <Grid container item justify="center" style={{ padding: "1rem" }} xs={3} sm={3} md={3} lg={3} xl={3} >

                <FormControl variant="outlined" fullWidth={true}>
                  <InputLabel id="RolLabel">Rol</InputLabel>
                  <Select labelId={"RolLabel"} id={"Rol"} name={rolesConst.find(e => e.id === u.id)} value={u.RolUser}
                    onChange={(e) => handlerChangeRolUser(e.target.value, u.id)}
                  >

                    {
                      rolesConst.map(item => <MenuItem key={item.id.toString()}
                        value={item.id}>{item.name}</MenuItem>)
                    }

                  </Select>
                </FormControl>

              </Grid>
            </React.Fragment>)
          }
        </Grid>

      </Grid>
    </>
  )
}
