import React, {useEffect, useState} from "react";
import {getUsersPendingLoad} from "./functions/FunctionsValidator";
import {Grid, TextField, InputAdornment, makeStyles} from "@material-ui/core";
import {EmptyData} from "../../components/gen/EmptyData";
import {Info} from "../../assets/app_const/AppConstants";
import {CardsUsers} from "./components/CardsUsers";
import SearchIcon from '@material-ui/icons/Search';

const Styles = makeStyles({
    marginInput: {
        margin: "1rem"
    }
})

export const RequestSignIn = () => {
    const classes = Styles();
    const [usersPendings, setUsersPendings] = useState({
        loading: true,
        users: []
    });

    const [searchName,setSearchName] = useState("");

    useEffect(() => {
        let mounted = true;
        if (mounted) {
            getUsersPendingLoad(setUsersPendings);
        }

        return () => {
            mounted = false;
        };
    }, []);


    if (usersPendings.loading) return <> cargando...</>


    if (usersPendings.users.length === 0)
        return <Grid container justify={"center"} alignContent={"center"}
                     direction={"column"}> <EmptyData not_data={Info.not_data_user}/>
        </Grid>;

    const updateListUsers =(id)=>{
        setUsersPendings(
            {
                ...usersPendings,
                users: usersPendings.users.filter(user => user.id !== id)
            }
        );
    }

    return <>
        <Grid container>
            <TextField
                onChange={(e)=>setSearchName(e.target.value)}
                className={classes.marginInput}
                variant={"outlined"}
                fullWidth={true}
                label={"Buscar por nombre"}
                InputProps={{
                    startAdornment: (
                        <InputAdornment position="start">
                            <SearchIcon/>
                        </InputAdornment>
                    ),
                }}
            />
            <Grid container justify={"center"}>
                {
                    usersPendings.users.filter((user)=>searchName.length<=0 ? true : user.FullName.toUpperCase().includes(searchName.toUpperCase()) ).map(user => <CardsUsers key={user.id} {...user} UpdateList={()=>updateListUsers(user.id)} />)
                }
            </Grid>
        </Grid>
    </>
}