import { localConfigure } from "../assets/app_const/AppConstants";
import { confirmedAuthToken } from "../views/LoginView/reducer/actionReducer";
export const confirmedToken = (dispatch) => {
  const token = localStorage.getItem(localConfigure.TOKENKEY);
  if (token) {
    dispatch(confirmedAuthToken(token));
  } else {
    localStorage.removeItem(localConfigure.TOKENKEY);
  }
};
