import React, { useEffect, useState } from 'react';

import { getInfoStateProject } from "./FuncionesCharts";
import { ResponsiveContainer, PieChart, Pie, Cell } from "recharts";
import { Box, CircularProgress, Grid } from "@material-ui/core";
import { BackgroundColor, colorsCharts } from "../../../assets/app_const/ColorsConst";
import { useHistory } from 'react-router-dom';
import { pathFront } from '../../../views/LoginView/app_const/AppConst';

export const ProjectState = () => {

    let history = useHistory();

    const [info, setInfo] = useState([]);

    useEffect(() => {
        let mounted = true;

        if (mounted) {
            getInfoStateProject(setInfo)
        }
        return () => {
            mounted = false
        };
    }, [setInfo]);


    if (info.length === 0) return <Grid container justify={"center"}><CircularProgress /></Grid>;

    return <Box width={"100%"} height={300} alignContent={"center"}>

        <ResponsiveContainer>
            <PieChart width={400} height={400}>
                <Pie dataKey="value" data={info} fill={BackgroundColor.primary_main} label={(props) => props.name + ` (${props.value})`} >
                    {info.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={colorsCharts[index]} onClick={(_)=>{
                            history.push(pathFront.searchProjectState+entry.id);
                        }} />
                    ))}
                </Pie>
            </PieChart>
        </ResponsiveContainer>


    </Box>


}

