import {Grid, Typography} from "@material-ui/core";
import React from "react";

export const ShowCaracteristicas = ({
                                        caract1, value1,
                                        caract2, value2,
                                        caract3, value3,
                                        caract4, value4
                                    }) => {
    const array = [
        {x: caract1, y: value1,},
        {x: caract2, y: value2,},
        {x: caract3, y: value3,},
        {x: caract4, y: value4}];

    return <>
        {
            array.map((c,i) => {
                if (!c.x) {
                    return "";
                }
                return (<React.Fragment key={i}>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6}>
                        <Typography variant={"body1"}>
                            {c.x}
                        </Typography>
                    </Grid>
                    <Grid container item xl={6} lg={6} md={6} sm={6} xs={6}

                    >
                        <Typography variant={"body1"}>
                            {c.y ?? "Sin informacin"}
                        </Typography>
                    </Grid>
                </React.Fragment>);

            })
        }
    </>
}