import {Button, Grid, makeStyles, TextField} from "@material-ui/core";
import React, {useState} from "react";
import {useDispatch} from "react-redux";
import {registerAction} from "../function/registerAction";

const styles = makeStyles({
    inputStyles: {
        margin: "1rem 0rem 1rem 0rem",
    },
});

export const FormRegister = ({functionChangeView}) => {

    const dispatch = useDispatch();

    const initFormRegister ={
        fullName: "",
        email: "",
        password: "",
        repeatPassword: "",
        RUT: "",
        socio: ""
    }

    const [Form, setForm] = useState(initFormRegister);

    const handlerChangeValueForm = (e) => {
        setForm({
            ...Form,
            [e.target.id]: e.target.value,
        });
    };

    const submitFormRegister = (e)=>{

        e.preventDefault();
        if(Form.password===Form.repeatPassword){
            dispatch(registerAction(Form.email,Form.password,Form.fullName));
            setForm(initFormRegister);
            functionChangeView();
        }


    }


    const dataForm = [
        {
            id: "email",
            value: Form.email,
            type: "email",
            label: "Ingresar email",
            error: false
        }, {
            id: "fullName",
            value: Form.fullName,
            label: "Ingresar nombre completo",
            error: false
        },
        {
            id: "password",
            value: Form.password,
            type: "password",
            label: "Ingresar contrasea",
            error: !(Form.password === Form.repeatPassword),
            helperText:"Las contraseas deben coincidir."
        },
        {
            id: "repeatPassword",
            value: Form.repeatPassword,
            type: "password",
            label: "Repetir contrasea",
            error: !(Form.password === Form.repeatPassword),
            helperText:"Las contraseas deben coincidir."

        }, {
            id: "socio",
            value: Form.socio,
            type: "string",
            label: "Socio / Categoria de socio",
            error: false

        }
    ];



    const classes = styles();
    return (
        <form onSubmit={submitFormRegister}>
            <Grid container spacing={5}>

                {
                    dataForm.map(input => {
                        return <Grid key={input.id}  item container xl={6} lg={6} md={6} sm={12} xs={12}>
                            <TextField
                                required
                                id={input.id}
                                label={input.label}
                                type={input.type}
                                variant="outlined"
                                value={input.value}
                                fullWidth={true}
                                error={input.error}
                                className={classes.inputStyles}
                                helperText={ input.error ? input.helperText : "" }
                                onChange={handlerChangeValueForm}
                            />
                        </Grid>
                    })
                }

            </Grid>

            <Grid container justify="center">
                <Button type="submit" variant="contained" color="secondary">
                    Crear cuenta
                </Button>
            </Grid>
        </form>
    );
};
