/*

  user not auth = {}

  user auth = {
    fullName: ,
    token:
  }

*/

import { AuthType } from "./Types";

export const AuthReducer = (state = {}, action = null) => {
  switch (action.type) {
    case AuthType.login:
      return {
        FullName: action.payload.FullName,
        Token: action.payload.Token,
        Rol: action.payload.Rol,
      };
    case AuthType.logout:
      return {};
    default:
      return state;
  }
};
