import { Button, Grid, makeStyles, TextField } from "@material-ui/core";
import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { Info } from "../../../assets/app_const/AppConstants";
import { BackgroundColor, snackColors } from "../../../assets/app_const/ColorsConst";
import { createActionActiveSnackbar } from "../../../reducer/actionUIReducer";
import { loginAction } from "../reducer/actionReducer";
const styles = makeStyles({
  inputStyles: {
    margin: "1rem 0rem 1rem 0rem",
  },
});

export const FormLogin = () => {
  const dispatch = useDispatch();
  const [Form, setForm] = useState({
    email: "",
    password: "",
  });

  const handlerChangeValueForm = (e) => {
    setForm({
      ...Form,
      [e.target.id]: e.target.value,
    });
  };

  const onSubmitLogin = (e) => {
    e.preventDefault();
    dispatch(createActionActiveSnackbar(Info.load_login, snackColors.loading));
    dispatch(loginAction(Form.email, Form.password));
  };

  const classes = styles();
  return (
    <form onSubmit={onSubmitLogin}>
      <TextField
        required
        id="email"
        label="Ingresar email"
        type="email"
        variant="outlined"
        value={Form.email}
        fullWidth={true}
        className={classes.inputStyles}
        onChange={handlerChangeValueForm}
      />
      <TextField
        required
        id="password"
        label="Contrasea"
        variant="outlined"
        type="password"
        value={Form.password}
        fullWidth={true}
        className={classes.inputStyles}
        onChange={handlerChangeValueForm}
      />

      <Grid container justify="center">
        <Button type="submit" variant="contained" style={{background: BackgroundColor.primary_main, color:"white"}}>
          Iniciar sesion
        </Button>
      </Grid>
    </form>
  );
};
