import { CircularProgress, Grid, makeStyles, Typography } from '@material-ui/core';
import { Send } from '@material-ui/icons';
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import {  getFormUpdateProjectView } from '../ProjectView/functions';
import { FormProjecto } from './components/FormProjecto';

const useStyles = makeStyles({
    gridForm: {
        background: "#eaeaea", minHeight: '85vh', marginTop: '1rem', 
        borderRadius:"1rem",
        padding:"2rem"
    }
})

export const InformadorUpdateProject = () => {
    const classes = useStyles();

    const {id} = useParams();

    const [Project, setProject] = useState(null)
    useEffect(() => {
        let mounted = true;
        if (mounted) {
            getFormUpdateProjectView(id, setProject)
        }
        return () => {
            mounted = false;
        };
    }, [id]);
    console.log(Project);
    if(Project==null) return <Grid container justify="center"> <CircularProgress/> </Grid>
    if(Project!=null){
        var form = {
            NombreProyecto: Project.NameProyecto,
        Estado: Project.Estado,
        Sector: Project.Clasificacion,
        Region: Project.Region,
        Comuna: Project.Comuna,
        PlazoEjecucion: Project.PlazoEjecucion,
        FechaLicitacion: Project.FechaLicitacion,
        FechaInicioObras: Project.FechaInicioObras,
        NombreMandante: Project.NameContratista,
        MontoProyecto: Project.MontoInversion,
        Longitud: Project.Longitud,
        Seccion: Project.Seccion,
        Pendiente: Project.Pendiente,
        MetodoConstructivo: Project.MetodoConstructivo,
        Oficinas: Project.DireccionOfIngenieria
    }
    }
    return (
        <>
            actualizar { id }
            <Grid container justify="center">
                <Grid item container xs={11} sm={11} md={11} lg={11} xl={11} className={classes.gridForm} direction="column" justify="center" alignItems="center">
                    <Send />
                    <Typography variant="h3"> Creador de proyecto</Typography>{"\n"}
                    <FormProjecto form={form} update={true} id={id} />
                </Grid>
            </Grid>
        </>
    )
}
