import React, {useState} from 'react';
import {Button, Grid, Typography} from "@material-ui/core";
import {CantidadTotalProject} from "./Charts/CantidadTotalProject";
import {ProjectState} from "./Charts/ProjectState";
import {ProjectRegion} from "./Charts/ProjectRegion";
import {ProjectTipo} from "./Charts/ProjectTipo";
import {BackgroundColor} from "../../assets/app_const/ColorsConst";

export const Charts = () => {

    const[stateTorta,setStateTorta]=useState(true);

    return <>
        <Grid container justify={"center"} style={{padding:"2rem"}}>

            <Grid container alignContent={"center"} justify={"space-between"}>
                <CantidadTotalProject />

            </Grid>
            <Grid container alignContent={"center"} item xl={6} lg={6} md={6} xs={12} sm={12}>
                {
                    stateTorta ?
                        <ProjectState />:<ProjectTipo/>
                }
                <Grid container justify={"center"} onClick={()=>setStateTorta(!stateTorta)}>
                    <Button style={{
                        background:BackgroundColor.primary_main,
                        color:"white"
                    }}>
                        <Typography variant={"body1"}>{(stateTorta) ? "Por tipo" : "Por estado"}</Typography>
                    </Button>
                </Grid>
            </Grid>
            <Grid container item xl={6} lg={6} md={6} xs={12} sm={12}>
                <ProjectRegion/>
            </Grid>



        </Grid>
    </>


}

