import {useSelector} from "react-redux";
import {admin, informador, socio, validador} from "../assets/app_const/AppConstants";
import {HomeSocioView} from "../views/HomeView/HomeSocioView";
import {HomeValidadorView} from "../views/HomeView/HomeValidadorView";
import {HomeInformadorView} from "../views/HomeView/HomeInformadorView";
import {HomeAdminView} from "../views/HomeView/HomeAdminView";
import React from "react";

export const ReturnHomeForRol = () => {
    const {Rol} = useSelector((state) => state.auth);


    switch (Rol) {
        case socio:
            return <HomeSocioView/>;
        case validador:
            return <HomeValidadorView/>;
        case informador:
            return <HomeInformadorView/>;
        case admin:
            return <HomeAdminView/>;
        default:
            return <></>;
    }
};
