import React from 'react';
import {Grid, makeStyles, Typography} from "@material-ui/core";
import {StringSearchView} from "../../assets/app_const/AppConstants";
import {Fotter} from "../../components/gen/Fotter";
import {FormSearch} from "./components/FormSearch";
import {useSelector} from "react-redux";
import {ResultadoSearch} from "./components/ResultadoSearch";

const Styles = makeStyles({
    GridBuscador: {
        padding: '2rem',
        minHeight:'73vh'
    },
})

export const SearchView = () => {
    const classes = Styles();

    const { res,viewResult } = useSelector((state) => state.search);

    if(viewResult) return <> <ResultadoSearch projects={res}/> </>;
    return <>
        <Grid container direction={"column"} className={classes.GridBuscador}>
            <Typography variant={"h1"}>
                {StringSearchView.title}
            </Typography>
            <Typography variant={"body1"}>
                <em>{StringSearchView.subtitle}</em>
            </Typography>

            <FormSearch/>

        </Grid>
        <Fotter/>
    </>
}
