/*


  ui = {
    snackbar:{
        show:true,
        msg:''
    },
  }

*/

import { TypeUi } from "./TypesUi";

export const UiReducer = (
  state = {
    Snackbar: {
      show: false,
      msg: "",
      color: null,
    },
    DrawerState:{
      open:false,
    }
  },
  action = null
) => {
  switch (action.type) {
    case TypeUi.openDrawer:
      return {
        ...state,
        DrawerState: {
          ...state.DrawerState,
          open:true
        }
      }
    case TypeUi.closeDrawer:
      return {
        ...state,
        DrawerState: {
          ...state.DrawerState,
          open:false
        }
      }
    case TypeUi.activateSnack:
      return {
        ...state,
        Snackbar: {
          show: true,
          msg: action.payload.msg,
          color: action.payload.color,
        },
      };
    case TypeUi.desactiveSnack:
      return {
        ...state,
        Snackbar: {
          show: false,
          msg: "",
          color: null,
        },
      };
    default:
      return state;
  }
};
