import {TypeSearch} from "./TypeSearch";


const formInit={
    NombreProyecto: "",
    Estado: "",
    Sector: "",
    Region: "",
    Comuna: "",
    PlazoEjecucion: "",
    FechaAprobacion: {
        from: null,
        to: null
    },
    FechaIngreso: {
        from: null,
        to: null
    },
    NombreMandante: "",
    MontoProyecto: [0, 100],
    Longitud: [0, 5000],
}

export const SearchReducer = (
    state = {
        res:[],
        form:formInit,
        viewResult:false
    },
    action = null
) => {
    switch (action.type) {
        case TypeSearch.AddSearch:
            return {
                ...state,
                form: action.payload.form
            }
        case TypeSearch.DeleteSearch:
            return{
                ...state,
                form: formInit
            }
        case TypeSearch.AddResult:
            return {
                ...state,
                res:action.payload.res
            };
        case TypeSearch.ViewResult:
            return {
                ...state,
                viewResult: true
            }
        case TypeSearch.CloseResult:
            return {
                ...state,
                viewResult: false
            }
        default:
            return state;
    }
};
