import { Drawer, ListItem, ListItemIcon, ListItemText } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { createActionDrawerClose, createActionLogOut } from "../../../views/LoginView/reducer/actionReducer";
import { DrawerOption } from "../../../assets/app_const/AppConstants";
import React from "react";
import { pathFront } from "../../../views/LoginView/app_const/AppConst";
import { useHistory } from "react-router-dom";
import { createActionCloseResult } from "../../../views/SearchView/functions/ActionSearch";

export const DrawerUi = () => {

    let history = useHistory();


    const { open } = useSelector((state) => state.ui.DrawerState);
    const { Rol } = useSelector((state) => state.auth);


    const dispatch = useDispatch();

    const closeDrawerUi = () => {
        dispatch(createActionDrawerClose());
    }

    const navigatorPush = (path) => {
        closeDrawerUi();
        if (path) {
            if (path === pathFront.searchProject) {
                dispatch(createActionCloseResult());
            }
            history.push(path);
        } else {
            dispatch(createActionLogOut());
            history.replace(pathFront.home);
        }
    }


    return <>
        <Drawer anchor={"right"} open={open} onClose={closeDrawerUi}>
            {DrawerOption(Rol).map((item, index) =>
                <ListItem key={index} button onClick={() => navigatorPush(item.path)}>
                    <ListItemIcon>{item.icon} </ListItemIcon>
                    <ListItemText primary={item.text} />
                </ListItem>)}
        </Drawer>
    </>
}