import api from "../../../axios/API";
import {pathApi} from "../../LoginView/app_const/AppConst";


export const ReturnProjectPreview= async (pagina=1,setProyectPreview,setPage)=>{
    const size=8;
    const skip= (pagina-1)*8 ;
    try{
        const resp = await api.post(pathApi.previwProject+`?skip=${skip}&size=${size}`);

        setProyectPreview({project: resp.data.data, loading: false});
    }catch (e) {
        setProyectPreview({project:[],loading:false});
        console.log(e);
    }

}


export const loadPageCant = async (setPage)=>{
    try{
        const resp = await api(pathApi.cantidadProject);
        setPage({pageNow:1,cant:resp.data.data});
    }catch (e) {
        console.log(e);
    }
}