import { AppBar, Box, Grid, makeStyles, Tab, Tabs } from "@material-ui/core";
import { ShowCaracteristicas } from "./ShowCaracteristicas";
import { BackgroundColor } from "../../assets/app_const/ColorsConst";
import { Assignment, CalendarToday, LocationOn } from "@material-ui/icons";


const useStyle = makeStyles({
    tabPanelStyle: {
        backgroundColor: BackgroundColor.cardProjectColor,
        color: 'black'
    }
})

export const Caracteristicas = (project) => {


    return <>

        <Grid container justify={"center"} style={{ padding: '3rem 1rem 0rem 1rem' }}>
            <AppBar position="static">
                <Tabs value={0} aria-label="PanelDate" variant="fullWidth" >
                    <Tab label="Fechas" icon={<CalendarToday />} />
                </Tabs>
                <TabPanel value={0} index={0} >
                    <ShowCaracteristicas caract1={"Fecha de licitacn: "} value1={project.DateLicitacion}
                        caract2={"Fecha de inicio de obras: "} value2={project.DateStart}
                        caract3={"Plazo de ejecucion: "} value3={project.Plazo}

                    />
                </TabPanel>

            </AppBar>
            <AppBar position="static">
                <Tabs value={0} aria-label="PanelCaract" variant="fullWidth" >
                    <Tab label="Caracteristicas" icon={<Assignment />} />
                </Tabs>
                <TabPanel value={0} index={0} >
                    <ShowCaracteristicas caract1={"Longitud en (km): "} value1={project.Long/1000}
                        caract2={"Seccion: "} value2={project.Secc}
                        caract3={"Pendiente: "} value3={project.Pendiente}

                    />
                </TabPanel>

            </AppBar>

            <AppBar position="static">
                <Tabs value={0} aria-label="PanelLocation" variant="fullWidth" >
                    <Tab label="Localizacion" icon={<LocationOn />} />

                </Tabs>
                <TabPanel value={0} index={0} >
                    <ShowCaracteristicas caract1={"Region: "} value1={project.Region}
                        caract2={"Comuna: "} value2={project.Comuna}
                        caract3={"Direccin oficinas: "} value3={project.Oficinas}

                    />
                </TabPanel>

            </AppBar>
        </Grid>

    </>
}

function TabPanel({ children, value, index }) {
    const classes = useStyle();
    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            className={classes.tabPanelStyle}
        >
            {value === index ? (
                <Box p={3} >
                    <Grid container spacing={5} >
                        {children}
                    </Grid>
                </Box>
            ) : <></>}
        </div>
    );
}