import {Button, Grid, makeStyles, Typography} from "@material-ui/core";
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import {buttons} from "../../../assets/app_const/AppConstants";
import CloseIcon from '@material-ui/icons/Close';
import {setUserActive} from "../functions/FunctionsValidator";
import {useDispatch} from "react-redux";
const Styles = makeStyles(
    {
        cardUser: {
            backgroundColor: "#eaeaea",
            padding: "2rem 2rem",
            margin: "1rem"
        },
        buttonsActive: {
            backgroundColor: "#11D800",
            color: "white",
            borderRadius: "2em",
            margin: "1rem",
            padding: "0.6rem"
        },buttonsReject: {
            backgroundColor: "#BB1C1A",
            color: "white",
            borderRadius: "2em",
            margin: "1rem",
            padding: "0.6rem"
        },
        marginIcon: {
            margin: "0rem 0.5rem 0rem 0rem"
        }
    }
)

export const CardsUsers = ({FullName, Email,id,UpdateList}) => {

    const dispatch = useDispatch();
    const classes = Styles();

    const hanlderUserButton=(condition)=>{
        dispatch(setUserActive(id,condition));
        UpdateList();
    }

    return <Grid container item xl={5} lg={5} md={5} xs={12} sm={12} >
        <Grid container className={classes.cardUser} direction={"column"} >
            <Typography variant={"h4"}>
                {FullName.toUpperCase()}
            </Typography>
            <Typography variant={"body1"}>
                <strong>Email:</strong> {Email.toUpperCase()}
            </Typography>
            <Grid container direction={"row"} justify={"center"}>

                <Button className={classes.buttonsActive} onClick={()=>hanlderUserButton(true)}>
                    <CheckCircleOutlineIcon className={classes.marginIcon}/>
                    {buttons.activeUser}
                </Button>
                <Button className={classes.buttonsReject} onClick={()=>hanlderUserButton(false)}>
                    <CloseIcon className={classes.marginIcon}/>
                    {buttons.rechazarUser}
                </Button>
            </Grid>
        </Grid>
    </Grid>
}